import React from 'react';

export class Bingkai extends React.Component {
	constructor(props){
		super(props);

		this.state={
			hoverBagian1 : false,
			hoverBagian2 : false,
			hoverBagian3 : false,
			hoverBagian4 : false
		}
	}

	handleClick = (e) => {
		this.props.selectedSection(e);
	}

	handleMouseOverBagian1 = () => {
		this.setState({hoverBagian1 : true});
	}

	handleMouseOverBagian2 = () => {
		this.setState({hoverBagian2 : true});
	}

	handleMouseOverBagian3 = () => {
		this.setState({hoverBagian3 : true});
	}

	handleMouseOverBagian4 = () => {
		this.setState({hoverBagian4 : true});
	}

	handleMouseLeaveBagian1 = () => {
		this.setState({hoverBagian1 : false});
	}

	handleMouseLeaveBagian2 = () => {
		this.setState({hoverBagian2 : false});
	}

	handleMouseLeaveBagian3 = () => {
		this.setState({hoverBagian3 : false});
	}

	handleMouseLeaveBagian4 = () => {
		this.setState({hoverBagian4 : false});
	}

	render(){
		const width = this.props.width;
		var fontSizeTitle, fontSizeTitleBg, fontAHeadLine;
		var fontSpacingTitleBg;

		switch(true) {
			case (width <= 480):
				fontSizeTitleBg = '55px';
				fontSpacingTitleBg = 4;
				fontSizeTitle = '25px';
				fontAHeadLine = '18px';
				break;
			case (width <= 600):
				fontSizeTitleBg = '65px';
				fontSpacingTitleBg = 10;
				fontSizeTitle = '35px';
				fontAHeadLine = '25px';
				break;
			case (width <= 800):
				fontSizeTitleBg = '80px';
				fontSpacingTitleBg = 10;
				fontSizeTitle = '35px';
				fontAHeadLine = '25px';
				break;
			case (width <= 1920):
				fontSizeTitleBg = '95px';
				fontSpacingTitleBg = 10;
				fontSizeTitle = '40px';
				fontAHeadLine = '28px';
				break;
			default:
				break;
		}

		const styles = {
			main: {
				position: 'absolute',
				height: '100%',
				width: '100%',
				boxSizing: 'border-box',
				overflow: 'hidden'
			},
			box: {
				position: 'relative',
				width: '100%',
				height: '100%',
				opacity: 1,
				display: 'flex',
				alignItems: 'center',
				textAlign: 'center',
				fontFamily: 'Poppins,sans-serif,Calibri,Arial'
			},
			bagian1 : {
				top: 0,
				left: 0,
				height: `${(width <= 800 ? '49%' : '50%')}`,
				width: `${(width <= 800 ? '100%' : '50%')}`,
				position: 'relative',
				display: 'block',
				boxSizing: 'border-box',
				backgroundImage: `url(${this.props.backgroundPart1})`,
				backgroundSize: 'cover',
				backgroundRepeat: 'no-repeat',
				backgroundPosition: 'center'
			},
			bagian2: {
				top: `${(width <= 800 ? '' : 0)}`,
				left: `${(width <= 800 ? '' : '50%')}`,
				height: `${(width <= 800 ? '17%' : '50%')}`,
				width: `${(width <= 800 ? '100%' : '50%')}`,
				position: `${(width <= 800 ? '' : 'absolute')}`,
				display: 'block',
				boxSizing: 'border-box',
				backgroundColor: '#222',
				borderBottom: `${(width <= 800 ? '1px solid #333' : '')}`,
				cursor: 'pointer'
			},
			bagian3: {
				top: `${(width <= 800 ? '' : '50%')}`,
				left: `${(width <= 800 ? '' : 0)}`,
				height: `${(width <= 800 ? '17%' : '50%')}`,
				width: `${(width <= 800 ? '100%' : '50%')}`,
				position: `${(width <= 800 ? '' : 'absolute')}`,
				display: 'block',
				boxSizing: 'border-box',
				backgroundColor: '#222',
				borderBottom: `${(width <= 800 ? '1px solid #333' : '')}`,
				cursor: 'pointer'
			},
			bagian4: {
				top: `${(width <= 800 ? '' : '50%')}`,
				left: `${(width <= 800 ? '' : '50%')}`,
				height: `${(width <= 800 ? '17%' : '50%')}`,
				width: `${(width <= 800 ? '100%' : '50%')}`,
				position: `${(width <= 800 ? '' : 'absolute')}`,
				display: 'block',
				boxSizing: 'border-box',
				backgroundColor: `${(width <= 800 ? '#222' : '#161515')}`,
				borderBottom: `${(width <= 800 ? '1px solid #333' : '')}`,
				cursor: 'pointer'
			},
			titleHeading: {
				position: 'absolute',
				bottom: 0,
				width: '100%',
				left: 0,
				textAlign: 'center',
				backgroundColor: 'rgba(0,0,0,0.8)',
				padding: '19 0 15'
			},
			ahHeadline: {
				fontSize: `${fontAHeadLine}`,
				fontWeight: 800,
				textTransform: 'uppercase',
				color: '#FFF'
			},
			ahGreet: {
				fontWeight: 300
			},
			ahWords: {
				fontWeight: 600,
				color: '#FBF31D'
			},
			titleBg: {
				color: 'rgba(255, 255, 255, 0.07)',
				fontSize: `${fontSizeTitleBg}`,
				left: 0,
				letterSpacing: `${fontSpacingTitleBg}`,
				lineHeight: 0.55,
				position: 'absolute',
				right: 0,
				top: 0,
				textTransform: 'uppercase',
				fontWeight: 800,
			},
			headerSection: {
				width: '100%',
				letterSpacing: 1,
				margin: '0 auto',
				fontSize: `${fontSizeTitle}`,
				position: 'relative',
				fontWeight: 900,
				textTransform: 'uppercase',
				textAlign: 'center',
				overflow: 'hidden',
				display: 'block',
				lineHeight: `${(width <= 800 ? '145%' : '110%')}`,
			},
			hp2one: {
				color: '#FFF',				
				display: `${this.state.hoverBagian2 ? '' : 'inline-block'}`,				
				position: 'relative',
				transition: `${this.state.hoverBagian2 ? '' : 'transform 0.3s'}`,
				transform: `${this.state.hoverBagian2 ? 'translate3d(0, -105%,0)' : ''}`,
			},
			hp2oneDummy:{
				color: '#FBF31D',
				position: 'absolute',
				top: `${this.state.hoverBagian2 ? '' : '105%'}`,
			},
			hp2two: {
				color: '#FBF31D',
				display: `${this.state.hoverBagian2 ? '' : 'inline-block'}`,
				top: `${this.state.hoverBagian2 ? '105%' : ''}`,
				position: 'relative',
				transition: `${this.state.hoverBagian2 ? '' : 'transform 0.3s'}`,
				transform: `${this.state.hoverBagian2 ? 'translate3d(0, -105%,0)' : ''}`
			},
			hp2twoDummy:{
				color: '#FFF',
				position: 'absolute',
				top: `${this.state.hoverBagian2 ? '' : '105%'}`,
			},
			hp3one: {
				color: '#FFF',				
				display: `${this.state.hoverBagian3 ? '' : 'inline-block'}`,				
				position: 'relative',
				transition: `${this.state.hoverBagian3 ? '' : 'transform 0.3s'}`,
				transform: `${this.state.hoverBagian3 ? 'translate3d(0, -105%,0)' : ''}`,
			},
			hp3oneDummy:{
				color: '#FBF31D',
				position: 'absolute',
				top: `${this.state.hoverBagian3 ? '' : '105%'}`,
			},
			hp3two: {
				color: '#FBF31D',
				display: `${this.state.hoverBagian3 ? '' : 'inline-block'}`,
				top: `${this.state.hoverBagian3 ? '105%' : ''}`,
				position: 'relative',
				transition: `${this.state.hoverBagian3 ? '' : 'transform 0.3s'}`,
				transform: `${this.state.hoverBagian3 ? 'translate3d(0, -105%,0)' : ''}`
			},
			hp3twoDummy:{
				color: '#FFF',
				position: 'absolute',
				top: `${this.state.hoverBagian3 ? '' : '105%'}`,
			},
			hp4one: {
				color: '#FFF',				
				display: `${this.state.hoverBagian4 ? '' : 'inline-block'}`,				
				position: 'relative',
				transition: `${this.state.hoverBagian4 ? '' : 'transform 0.3s'}`,
				transform: `${this.state.hoverBagian4 ? 'translate3d(0, -105%,0)' : ''}`,
			},
			hp4oneDummy:{
				color: '#FBF31D',
				position: 'absolute',
				top: `${this.state.hoverBagian4 ? '' : '105%'}`,
			},
			hp4two: {
				color: '#FBF31D',
				display: `${this.state.hoverBagian4 ? '' : 'inline-block'}`,
				top: `${this.state.hoverBagian4 ? '105%' : ''}`,
				position: 'relative',
				transition: `${this.state.hoverBagian4 ? '' : 'transform 0.3s'}`,
				transform: `${this.state.hoverBagian4 ? 'translate3d(0, -105%,0)' : ''}`
			},
			hp4twoDummy:{
				color: '#FFF',
				position: 'absolute',
				top: `${this.state.hoverBagian4 ? '' : '105%'}`,
			},			
			centerAlign: {				
				margin: '0 auto',
				width: '100%',
				position: 'relative',
				textAlign: 'center'
			}
		}

		const titleBg2 = this.props.section2.titleBg;
		const title2 = this.props.section2.title;

		const titleBg3 = this.props.section3.titleBg;
		const title3 = this.props.section3.title;

		const titleBg4 = this.props.section4.titleBg;
		const title4 = this.props.section4.title;		

		let subtitle2 = title2.split('|');
		let subtitle3 = title3.split('|');
		let subtitle4 = title4.split('|');

		let anim2In = (
			<React.Fragment>				
				<span style={styles.hp2one}>
					<span style={styles.hp2oneDummy}>{subtitle2[0]}</span>
					{subtitle2[0]}
				</span>
				&nbsp;
				<span style={styles.hp2two}>
					<span style={styles.hp2twoDummy}>{subtitle2[1]}</span>
					{subtitle2[1]}
				</span>
			</React.Fragment>
		);

		let anim2Out = (
			<React.Fragment>
				<span style={styles.hp2one}>
					<span style={styles.hp2oneDummy}>{subtitle2[0]}</span>
					{subtitle2[0]}
				</span>
				&nbsp;
				<span style={styles.hp2two}>
					<span style={styles.hp2twoDummy}>{subtitle2[1]}</span>
					{subtitle2[1]}
				</span>
			</React.Fragment>
		);

		let anim3In = (
			<React.Fragment>				
				<span style={styles.hp3one}>
					<span style={styles.hp3oneDummy}>{subtitle3[0]}</span>
					{subtitle3[0]}
				</span>
				&nbsp;
				<span style={styles.hp3two}>
					<span style={styles.hp3twoDummy}>{subtitle3[1]}</span>
					{subtitle3[1]}
				</span>
			</React.Fragment>
		);

		let anim3Out = (
			<React.Fragment>
				<span style={styles.hp3one}>
					<span style={styles.hp3oneDummy}>{subtitle3[0]}</span>
					{subtitle3[0]}
				</span>
				&nbsp;
				<span style={styles.hp3two}>
					<span style={styles.hp3twoDummy}>{subtitle3[1]}</span>
					{subtitle3[1]}
				</span>
			</React.Fragment>
		);

		let anim4In = (
			<React.Fragment>				
				<span style={styles.hp4one}>
					<span style={styles.hp4oneDummy}>{subtitle4[0]}</span>
					{subtitle4[0]}
				</span>
				&nbsp;
				<span style={styles.hp4two}>
					<span style={styles.hp4twoDummy}>{subtitle4[1]}</span>
					{subtitle4[1]}
				</span>
			</React.Fragment>
		);

		let anim4Out = (
			<React.Fragment>
				<span style={styles.hp4one}>
					<span style={styles.hp4oneDummy}>{subtitle4[0]}</span>
					{subtitle4[0]}
				</span>
				&nbsp;
				<span style={styles.hp4two}>
					<span style={styles.hp4twoDummy}>{subtitle4[1]}</span>
					{subtitle4[1]}
				</span>
			</React.Fragment>
		);

		return(
			<React.Fragment>
				<div style={styles.main}>
	 				<div key="key1" style={styles.bagian1} 
	 					onMouseOver={this.handleMouseOverBagian1.bind(this)} 
	 					onMouseLeave={this.handleMouseLeaveBagian1.bind(this)}>
	 					<div style={styles.box}>
		 					<div style={styles.titleHeading}>
		 						<h3 style={styles.ahHeadline}>
		 							<span style={styles.ahGreet}>Hi There ! I'm</span>
		 							&nbsp;
		 							<span style={styles.ahWords}>Riady Santoso</span>
		 						</h3>
		 					</div>
	 					</div>
	 				</div>
	  				<div key="key2" style={styles.bagian2}
	 					onMouseOver={this.handleMouseOverBagian2.bind(this)} 
	 					onMouseLeave={this.handleMouseLeaveBagian2.bind(this)}
	 					onClick={this.handleClick.bind(this,'resume')}>
	  					<div style={styles.box}>
		  					<div style={styles.centerAlign}>
		  						<span style={styles.titleBg}>{titleBg2}</span>
		  						<h2 style={styles.headerSection}>
		  							{(this.state.hoverBagian2 ? anim2In : anim2Out)}
		  						</h2>
		  					</div>
	  					</div>
	  				</div>
	 				<div key="key3" style={styles.bagian3}
	 					onMouseOver={this.handleMouseOverBagian3.bind(this)} 
	 					onMouseLeave={this.handleMouseLeaveBagian3.bind(this)}>
	 					<div style={styles.box}>
		 					<div style={styles.centerAlign}>
		 						<span style={styles.titleBg}>{titleBg3}</span>
		 						<h2 style={styles.headerSection}>
		  							{(this.state.hoverBagian3 ? anim3In : anim3Out)}
		  						</h2>
	  						</div>
	 					</div>
	 				</div>
	  				<div key="key4" style={styles.bagian4}
	 					onMouseOver={this.handleMouseOverBagian4.bind(this)} 
	 					onMouseLeave={this.handleMouseLeaveBagian4.bind(this)}>
	  					<div style={styles.box}>
		 					<div style={styles.centerAlign}>
		 						<span style={styles.titleBg}>{titleBg4}</span>
		 						<h2 style={styles.headerSection}>
		  							{(this.state.hoverBagian4 ? anim4In : anim4Out)}
		  						</h2>
	  						</div>
  						</div>
	  				</div>
				</div>
			</React.Fragment>		
		);
	}
}


