import React from 'react';
import {SizeMe} from 'react-sizeme';
import {Bingkai} from '../Components/Bingkai';
import AssetMe from '../Assets/me.jpg';

import Slide from '@material-ui/core/Slide';
import {HalamanResume} from '../Pages/HalamanResume';

export class HalamanUtama extends React.Component{	
	constructor(props){
		super(props);
		this.state = {
			activePage : 'utama',
		};
		this.handleSelectedSection = this.handleSelectedSection.bind(this);
		this.handleBack = this.handleBack.bind(this);
	}

	handleSelectedSection = (e) => {
		this.setState({activePage : e});
	}

	handleBack = () => {
		this.setState({activePage : 'utama'});
	}

	render(){
		const section2 = {
			titleBg: 'resume',
			title: 'about|me'
		};
		const section3 = {
			titleBg: 'works',
			title: 'my|portfolio'
		};
		const section4 = {
			titleBg: 'contact',
			title: 'get|in touch'
		};	
		
		if (this.state.activePage === 'utama') {
			return(
				<React.Fragment>
					<SizeMe>
					{({size}) =>
					<Slide direction="right" in>
	      			<Bingkai backgroundPart1={AssetMe} width={size.width}
	      					 section2={section2}
	      					 section3={section3}
	      					 section4={section4}
	      					 selectedSection={this.handleSelectedSection}
	      			/>
	      			</Slide>
	      			}
	      			</SizeMe>
	    		</React.Fragment>
			);
		} else {
			switch(this.state.activePage){
				case 'resume':
					return(
						<React.Fragment>
							<SizeMe>
							{({size}) =>
								<HalamanResume width={size.width} back={this.handleBack}/>
	      					}
	      					</SizeMe>							
						</React.Fragment>
					)
				default:
					break;
			}
		}
	}
}


